.ghub-accordion-item{
     overflow: hidden;
     .ghub-accordion-title{
          cursor: pointer;    
          .ghub-accordion-icon{
               transition: 0.3s;
          }
     }
     .ghub-accordion-content{
          overflow: hidden;
          &.ghub-accordion-content-animation{
             transition: 0.3s;
          }
     }
     &.ghub-active-accordion{
          .ghub-accordion-title{
               .ghub-accordion-icon{
                    transform: rotate(-180deg);
                    &.ghub-accordion-plus-operator{
                         transform: rotate(-225deg);
                    }
               }
               
          }
          
     }
}