.ghub-accordion-title{
    &.justify-content-left {
      .ghub-accordion-title-inner-accordion{
        justify-content: flex-start;
        .ghub-accordion-title-icon-accordion{
          justify-content: flex-start;
        }
      }
    }

    &.justify-content-right {
      .ghub-accordion-title-inner-accordion{
        justify-content: flex-end;
        .ghub-accordion-title-icon-accordion{
            justify-content: flex-end;
        }
      }
    }

    &.justify-content-center {
      .ghub-accordion-title-inner-accordion{
        justify-content: center;
        .ghub-accordion-title-icon-accordion{
            justify-content: center;
        }
      }
    }

    &.justify-content-space-between {
      .ghub-accordion-title-inner-accordion{
        justify-content: space-between;
        .ghub-accordion-title-icon-accordion{
            justify-content: space-between;
        }
      }
    }
    .ghub-accordion-title-inner-accordion{
          display: flex;
          align-items: center;
          .wp-block-heading, p{
               margin: 0;
          }
          .ghub-accordion-title-icon-accordion{
            display: flex;
            gap: var(--ghub-accordion-icon-spacing);
            align-items: center;
          }
    }
    &.ghub-accordion-icon-position-left{
      .ghub-accordion-title-inner-accordion{
        .ghub-accordion-title-icon-accordion{
          width: 100%;
        }
      }
    }
}