@import "./blocks-library/accordion/common.scss";
@import "./blocks-library/accordion-item/common.scss";
@import "./blocks-library/accordion-title/common.scss";
@import "./blocks-library/accordion-content/common.scss";

.ghub-accordion{
     display: flex;
     flex-direction: column;
     gap: var(--ghub-accordion-item-gap,20px);

     .ghub-accordion-item{
          position: relative;
          
          .ghub-accordion-content-initialized {
               opacity: 0;
               position: absolute;
          }
     }
}