.ghub-accordion{
     overflow: hidden;

      .ghub-accordion-item{
          &.ghub-accordion-hide{
               display: none;
          }
          transition: 0.3s;
          background: var(
               --ghub-accordion-item-bg-color,
               var(--ghub-accordion-item-gradient-color,#f0f0f0) 
          );
          padding-top: var(--ghub-accordion-item-padding-top);
          padding-right: var(--ghub-accordion-item-padding-right);
          padding-bottom: var(--ghub-accordion-item-padding-bottom);
          padding-left: var(--ghub-accordion-item-padding-left);
          border-top-left-radius: var(--ghub-accordion-item-top-left-radius);
          border-top-right-radius: var(
               --ghub-accordion-item-top-right-radius
          );
          border-bottom-left-radius: var(
               --ghub-accordion-item-bottom-left-radius
          );
          border-bottom-right-radius: var(
               --ghub-accordion-item-bottom-right-radius
          );
          border-top: var(--ghub-accordion-item-border-top);
          border-right: var(--ghub-accordion-item-border-right);
          border-bottom: var(--ghub-accordion-item-border-bottom);
          border-left: var(--ghub-accordion-item-border-left);

          .ghub-accordion-title{
               color: var(--ghub-accordion-item-title-color);
               background: var(--ghub-accordion-item-title-bg-color, var(--ghub-accordion-item-title-gradient-color));
               border-top-left-radius: var(--ghub-accordion-item-title-top-left-radius);
               border-top-right-radius: var(
                    --ghub-accordion-item-title-top-right-radius
               );
               border-bottom-left-radius: var(
                    --ghub-accordion-item-title-bottom-left-radius
               );
               border-bottom-right-radius: var(
                    --ghub-accordion-item-title-bottom-right-radius
               );
               border-top: var(--ghub-accordion-item-title-border-top);
               border-right: var(--ghub-accordion-item-title-border-right);
               border-bottom: var(--ghub-accordion-item-title-border-bottom);
               border-left: var(--ghub-accordion-item-title-border-left);
               padding-top: var(--ghub-accordion-item-title-padding-top);
               padding-right: var(--ghub-accordion-item-title-padding-right);
               padding-bottom: var(--ghub-accordion-item-title-padding-bottom);
               padding-left: var(--ghub-accordion-item-title-padding-left);
          }
          .ghub-accordion-content{
               .ghub-accordion-content-inner-accordion{
                    color: var(--ghub-accordion-item-content-color);
                    background: var(--ghub-accordion-item-content-bg-color,var(--ghub-accordion-item-content-gradient-color));
                    border-top-left-radius: var(--ghub-accordion-item-content-top-left-radius);
                    border-top-right-radius: var(
                         --ghub-accordion-item-content-top-right-radius
                    );
                    border-bottom-left-radius: var(
                         --ghub-accordion-item-content-bottom-left-radius
                    ); 
                    border-bottom-right-radius: var(
                         --ghub-accordion-item-content-bottom-right-radius
                    );
                    border-top: var(--ghub-accordion-item-content-border-top);
                    border-right: var(--ghub-accordion-item-content-border-right);
                    border-bottom: var(--ghub-accordion-item-content-border-bottom);
                    border-left: var(--ghub-accordion-item-content-border-left);
                    padding-top: var(--ghub-accordion-item-content-padding-top);
                    padding-right: var(--ghub-accordion-item-content-padding-right);
                    padding-bottom: var(--ghub-accordion-item-content-padding-bottom);
                    padding-left: var(--ghub-accordion-item-content-padding-left);
               }

          }
          &.ghub-active-accordion {
               background: var(--ghub-accordion-item-active-bg-color,var(--ghub-accordion-item-active-gradient-color,var(--ghub-accordion-item-bg-color,var(--ghub-accordion-item-gradient-color,#f0f0f0))));
               .ghub-accordion-title{
                    color: var(--ghub-accordion-item-active-title-color,var(--ghub-accordion-item-title-color));
                    background: var(--ghub-accordion-item-title-bg-active-color,var( --ghub-accordion-item-title-active-gradient-color,var(--ghub-accordion-item-title-bg-color, var(--ghub-accordion-item-title-gradient-color))));
               }
          }
          .ghub-accordion-content-inner-accordion {
               transition: 0.3s;
          }
     }

}